.sendForm {
    display: flex;
    align-items: center;
    margin-top: 20px;
}

.textarea {
    flex: 1;
    min-height: 40px;
    resize: none;
    padding: 10px;
    border-radius: 20px;
    border: 1px solid #ccc;
    font-size: 1em;
    margin-right: 10px;


}

.button {
    width: 120px;
    height: 40px;
    background-color: #0066CC;
    color: #FFFFFF;
    border-radius: 20px;
    border: none;
    padding: 10px 10px;
}

.button:hover {
border: 1px solid #181c32;
}

.button:active {
background-color: #007bff;
}
