.radio {
    margin-right: 10px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid #000;
    cursor: pointer;
    appearance: none;
}

.radio:hover {
    background-color: #78caea; /* Черный цвет фона для отмеченной радиокнопки */
}

.label {
    display: flex;
    align-items: center;
    font-size: 16px;
    cursor: pointer;
}

.options {
    display: flex;
    flex-direction: column;
    gap: 10px;
}
