.friendMessage {
    max-width: 800px;
    margin: 10px 10px;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.friendImageAndText {
    display: flex;
    align-items: center;
}

.friendImageAndText img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.friendText {
    background-color: #bbc5d0;
    width: 627px;
    height: 60px;
    border-radius: 5px 5px 0 5px;
    display: flex;
    justify-content: center;
    align-items: flex-end;

}

.friendText:after {
    background-color: #007bff;
    color: #181616;
    align-self: flex-end;
    height: auto;
}

.friendName {
    font-size: 20px;
    font-weight: bold;
    color: #23231f;
    margin-bottom: 5px;

}

.friendMessageText {
    width: 527px;
    height: 60px;
    border-radius: 5px 5px 5px 0;
    background-color: #bbc5d0;
    color: #23231f;
    align-self: flex-start;
}

.friendTime {
    text-align: left;
    color: #666;
    font-size: 12px;
    margin-top: 5px;
}
