.input:focus {
}

.input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    box-sizing: border-box;
    outline: none;
}

.inputWrapper {
    margin-bottom: 15px;
    position: relative;
}

.superInput {
    border-color: blue;
}

.errorInput {
    border-color: red;
}

.errorInput:focus {
}

.error {
    border-color: red;
}
