.loader {
    width: 108px;
    height: 108px;
    border: 8px solid rgba(0, 0, 0, 0.1);
    border-top: 8px solid #6a0dad;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    box-sizing: border-box;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}