.select {
    width: 150px; /* Задает ширину select */
    padding: 5px; /* Добавляет отступы внутри select */
    font-size: 16px; /* Размер текста */
    border: 1px solid #ccc; /* Цвет границы */
    border-radius: 4px; /* Слегка скругленные углы */
    appearance: none; /* Убирает стандартный вид браузера */
    background-color: #fff; /* Белый фон */
    cursor: pointer; /* Изменение курсора при наведении */
    outline: none; /* Убирает рамку при фокусе */
}

.select:focus {
    border-color: #007BFF; /* Синий цвет границы при фокусе */
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); /* Тень для акцента на элементе */
}


