.button {
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    font-size: 16px;
}

.disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

.secondary {
    background-color: grey;
    color: white;
}

.default {
    background-color: blue;
    color: white;
}

.red {
    background-color: red;
    color: white;
}

