.message {
    max-width: 800px;
    margin: 10px 10px;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    align-items: end;
    text-align: left;


}

.imageAndText {
    display: flex;
    /*align-items: flex-end;*/

}

.imageAndText img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    }

.text {
    width: 627px;
    height: 60px;
    border-radius: 5px 5px 0 5px;
    background-color: #007bff;
    display: flex;
    justify-content: right;
    align-items: flex-end;
    text-align: left;

}

.text:after {
    background-color: #007bff;
    color: #181616;
    align-self: flex-end;
    height: auto;
}

.name {
    font-size: 20px;
    font-weight: bold;
    color: #23231f;
    margin-bottom: 5px;
    text-align: left;

}

.messageText {
    color: #fff;
    align-self: flex-end;
}

.time {
    text-align: right;
    color: #666;
    font-size: 12px;
    margin-top: 5px;
}